import './commentform.css';
// @ts-ignore
import Textarea from 'react-textarea-count';
import PropTypes from 'prop-types';
import { useCallback, useState } from 'react';
const currentUser = {
    id: 1,
    avatar: "data:image/jpeg;base64,",
}
const CommentForm = ({ limit, rows, cols }: PropsType) => {
    const [content, setContent] = useState('');
    const setFormattedContent = useCallback(
        text => {
            setContent(text.slice(0, limit));
        },
        [limit, setContent]
    );
    const handleSubmit = (e: any) => {
        alert(content);
    }

    return (
        <form onSubmit={handleSubmit}>
            <div className="comment-form-wrapper">
                <div className="wrapper d-flex">
                    <img src={currentUser.avatar} alt="avatar" className="avatar" />
                    <textarea
                        rows={rows}
                        cols={cols}
                        onChange={event => setFormattedContent(event.target.value)}
                        value={content}
                    />

                </div>
                <div className="submit-wrapper">
                    <span className={"d-inline-block size-extremely-small " + ((limit - content.length > 5) ? " color-light" : ' color-secondary')}>{limit - content.length}</span> <span className="d-inline-block color-light size-extremely-small br-light-1 px-2">characters left</span>
                    <button className="btn-text color-secondary size-extremely-small" type="submit">submit</button>
                </div>
            </div>
        </form>

    )
}

interface PropsType {
    rows?: number,
    cols?: number,
    limit: number
}

export default CommentForm;
